import React, { useContext, useEffect } from 'react';
import { BasicContext } from '../contexts/BasicContext';
import { notification } from 'antd';

export interface SaveLoadFeatureProps {
    code: string;
    onSetCode: (code: string) => void;
}

const SaveLoadFeature: React.FC<SaveLoadFeatureProps> = ({
    code,
    onSetCode,
}) => {
    const info = useContext(BasicContext);

    // save
    useEffect(() => {
        if (info) {
            return info.save.addListener(({ filename }) => {
                const allProgramsData = localStorage.getItem('files') || '{}';
                let allPrograms: Record<string, string> = {};
                try {
                    allPrograms = JSON.parse(allProgramsData);
                } catch (e) {}

                allPrograms[filename.toUpperCase()] = code;

                localStorage.setItem('files', JSON.stringify(allPrograms));

                // show an alert
                notification.success({
                    message: 'SAVE completed',
                    description: (
                        <div>
                            Your program has been saved to local storage. Use{' '}
                            <code>LOAD "{filename}"</code> to load this program
                            again. Note that you will <strong>lose</strong> this
                            program if you clear your local storage.
                        </div>
                    ),
                });
            });
        }
    }, [info, code, onSetCode]);

    // load
    useEffect(() => {
        if (info) {
            return info.load.addListener((filename) => {
                const allProgramsData = localStorage.getItem('files') || '{}';
                let allPrograms: Record<string, string> = {};
                try {
                    allPrograms = JSON.parse(allProgramsData);
                } catch (e) {}

                const program = allPrograms[filename.toUpperCase()];

                if (!program) {
                    notification.error({
                        message: 'LOAD failed.',
                        description: `Could not find program "${filename}". Did you accidently clear your local storage?`,
                    });
                } else {
                    onSetCode(program);

                    notification.success({
                        message: 'LOAD finished.',
                        description: `Successfully loaded program "${filename}".`,
                    });
                }
            });
        }
    }, [info]);

    return null;
};

export default SaveLoadFeature;
